/**
 * 
 * @param Object|string|number time 
 * @param string cFormat  
 * @return string|null  
 */
 export function parseTime(time: string | number | Date, cFormat?: string): string | void {
  // 
  if (arguments.length === 0 || !time) {
    return;
  }
  // 
  const format: string = cFormat || 'y-M-d h:m:s'
  let date: Date
  if (typeof time === 'object') {
    date = time
  } else {
    if (typeof time === 'string') {
      if (/^[0-9]+$/.test(time)) {
        // support "1548221490638"
        time = parseInt(time)
      } else {
        // https://stackoverflow.com/questions/4310953/invalid-date-in-safari
        //time = time.replace(/-/gm, '/')
      }
    }

    // 
    if (typeof time == 'number' && time.toString().length === 10) {
      time = time * 1000
    }
    date = new Date(time)
  }

  // 
  const formatObj: any = {
    y: date.getFullYear(),
    M: date.getMonth() + 1,
    d: date.getDate(),
    h: date.getHours(),
    m: date.getMinutes(),
    s: date.getSeconds(),
    a: date.getDay()
  }

  // 
  const time_str: string = format.replace(/([yMdhmsa])+/g, (result, key) => {
    const value: number = formatObj[key]
    // getDay() 0
    if (key === 'a') {
      return ['', '', '', '', '', '', ''][value]
    }
    return value.toString().padStart(2, '0')
  })
  return time_str
}

/**
 * 
 */
export const validateZoneName = (rule: any, value: any, callback: any): void => {
  if (!value) {
    callback(new Error(""));
  }
  const arr = value.trim().split(/\s+/)
  if (arr.length < 1) {
    return callback(new Error(""))
  }
  for (const name of arr) {
    if (!/^\w+\.\w+$/.test(name)) {
      return callback(new Error("" + name))
    }
  }
  callback()
}

/**
 * 
 * @param nameStr 
 * @returns 
 */
export const splitZoneName = (nameStr: string): string[] => {
  return Array.from(new Set(nameStr.trim().split(/\s+/)))
}